import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { TestComponent } from './test/test.component';
import { QuestionListComponent } from './question-list/question-list.component';
import { AddQuestionComponent } from './add-question/add-question.component';
import { ViewQuestionComponent } from './view-question/view-question.component';
import { QuestionListTaggedComponent } from './question-list-tagged/question-list-tagged.component';

const routes: Routes = [
    { path: '', redirectTo: '/questions', pathMatch: 'full' },
    { path: 'test', component: TestComponent },
    { path: 'questions', component: QuestionListComponent },
    { path: 'questions/tagged/:tag', component: QuestionListTaggedComponent },
    { path: 'questions/new', component: AddQuestionComponent },
    { path: 'questions/:question', component: ViewQuestionComponent }
];

@NgModule({
    imports: [ RouterModule.forRoot(routes) ], //, { enableTracing: true }
    exports: [ RouterModule ],
    declarations: []
})
export class AppRoutingModule { }



