import {Injectable} from '@angular/core';
import {Subject} from 'rxjs/Rx';
import {Web3Service} from './util/web3.service';
import dappoverflow_artifacts from '../../build/contracts/DappOverflow.json';

@Injectable()
export class DataService {

    storage: string;
    DappOverflow: any;
    deployedDappOverflow: any;
    public accountsObservable = new Subject<string[]>();

    public constructor(web3Service: Web3Service) {
      web3Service.accountsObservable.subscribe((accounts) => {
        this.storage = accounts[0];
        this.accountsObservable.next(accounts);
      });
      web3Service.artifactsToContract(dappoverflow_artifacts)
        .then((DappOverflowAbstraction) => {
          this.DappOverflow = DappOverflowAbstraction;
          this.deployedDappOverflow = this.DappOverflow.deployed();
      });
    }

    public async getAccount() {
      if (this.storage == undefined) {
        const delay = new Promise(resolve => setTimeout(resolve, 100));
        await delay;
        return await this.getAccount();
      }
      return this.storage;
    }

    public async getDappOverflow() {
      if (this.DappOverflow == undefined) {
        const delay = new Promise(resolve => setTimeout(resolve, 100));
        await delay;
        return await this.getDappOverflow();
      }
      return this.DappOverflow;
    }

}



