import {Component, OnInit} from '@angular/core';
import { DataService } from '../dataservice';

@Component({
  selector: 'test',
  templateUrl: './test.component.html',
  styleUrls: ['./test.component.css']
})
export class TestComponent implements OnInit {
  account: string;
  DappOverflow: any;
  deployedDappOverflow: any;

  model = {
    name: '',
    address: 0,
    amount: 5,
    receiver: '',
    balance: 0,
    account: '',
    question: '',
    answer: '',
    questionId: 0
  };

  status = '';

  blockNumber = 0;

  users = [];
  questions = [];
  answers = [];

  constructor(private dataService: DataService) {
  }

  ngOnInit(): void {

    this.dataService.getAccount().then((account) => {
      this.account = account;
    });

    this.dataService.accountsObservable.subscribe((accounts) => {
      if (this.account != accounts[0]) {
        this.account = accounts[0];
      }
    });

    this.dataService.getDappOverflow().then((DappOverflow) => {
        this.DappOverflow = DappOverflow;
        this.deployedDappOverflow = this.DappOverflow.deployed();
        this.getUsers();
        this.getQuestions();
        this.watchEvents(this);

    });
  }

  watchEvents(e) {
    e.deployedDappOverflow.then(function(instance) {
       instance.UserCreated().watch((err, response) => {
        if (!err) {
           if (e.blockNumber < response.blockNumber) {
             e.users.push(e.hex2a(response.args.name) + " [" + response.args.userAddress + "]");
             e.blockNumber = response.blockNumber;
           }
        } else {
          console.log(err);
        }
       });
       instance.QuestionSubmitted().watch((err, response) => {
        if (!err) {
           if (e.blockNumber < response.blockNumber) {
             e.questions.push(response.args.questionId + ": " + response.args.question + "()");
             e.blockNumber = response.blockNumber;
           }
        } else {
          console.log(err);
        }
       });
       instance.AnswerSubmitted().watch((err, response) => {
        if (!err) {
           if (e.blockNumber < response.blockNumber) {
             e.blockNumber = response.blockNumber;
           }
        } else {
          console.log(err);
        }
       });
    });
  }

  setStatus(status) {
    this.status = status;
  }

  setUsers(users) {
    this.users = users;
  }

  setQuestions(questions) {
    this.questions = questions;
  }

  setAnswers(answers) {
    this.answers = answers;
  }

  async createUser() {
    if (!this.DappOverflow) {
      this.setStatus('DappOverflow is not loaded, unable to create user');
      return;
    }

    const userName = this.model.name;
    const userAddress = this.model.address;



    this.setStatus('Initiating creation... (please wait)');

    try {
      const deployedDappOverflow = await this.DappOverflow.deployed();
      const creation = await deployedDappOverflow.createUser(userName, userAddress, {from: this.account});
      if (!creation) {
        this.setStatus('Creation failed!');
      } else {
        this.setStatus('Creation completed!');
      }
    } catch (e) {
      console.log(e);
      this.setStatus('Error creating user; see log.');
    }

  }

  // checkUser() {
  //   for (let i in this.users) {
	// 		if (this.users[i].id == parseInt(args.questionId)) {
	// 			this.questions[i].votes = args.votes;
	// 			return this.questions[i];
	// 		}
	// 	}
  // }

  async submitQuestion() {
    if (!this.DappOverflow) {
      this.setStatus('DappOverflow is not loaded, unable to send transaction');
      return;
    }

    const userQuestion = this.model.question;

    this.setStatus('Initiating question... (please wait)');

    try {
      const deployedDappOverflow = await this.DappOverflow.deployed();
      const transaction = await deployedDappOverflow.submitQuestion(userQuestion, userQuestion, '', this.account, {from: this.account});
      if (!transaction) {
        this.setStatus('Question failed!');
      } else {
        this.setStatus('Question completed!');
      }
    } catch (e) {
      console.log(e);
      this.setStatus('Error sending question; see log.');
    }

  }

  async submitAnswer() {
    if (!this.DappOverflow) {
      this.setStatus('DappOverflow is not loaded, unable to send transaction');
      return;
    }

    const userAnswer = this.model.answer;
    const userQuestionId = this.model.questionId;

    this.setStatus('Initiating answer... (please wait)');

    try {
      const deployedDappOverflow = await this.DappOverflow.deployed();
      const transaction = await deployedDappOverflow.submitAnswer(userAnswer, this.account, userQuestionId, {from: this.account});
      if (!transaction) {
        this.setStatus('Answer failed!');
      } else {
        this.setStatus('Answer completed!');
        this.getQuestions();
      }
    } catch (e) {
      console.log(e);
      this.setStatus('Error sending answer; see log.');
    }

  }

  async getUsers() {
    console.log('Getting users');

    try {
      const deployedDappOverflow = await this.DappOverflow.deployed();
      const dappOverflowLength = await deployedDappOverflow.getUsers();
      var usersLen = dappOverflowLength[0].length;
      var users = [];
      for (var i=0; i<usersLen; i++) {
        users.push(this.hex2a(dappOverflowLength[0][i]) + " [" + dappOverflowLength[1][i] + "]")
      }
      this.setStatus('Found users: ');
      this.setUsers(users);
    } catch (e) {
      console.log(e);
      this.setStatus('Error getting length; see log.');
    }
  }

  async getQuestions() {
    console.log('Getting questions');

    try {
      const deployedDappOverflow = await this.DappOverflow.deployed();
      const dappOverflowQuestions = await deployedDappOverflow.getQuestions();
      var questionsLen = dappOverflowQuestions.length;
      var questions = [];
      var answers = [];
      for (var i=0; i<dappOverflowQuestions[0].length; i++) {
        answers = [];
        const dappOverflowQuestion = await deployedDappOverflow.getQuestion(dappOverflowQuestions[0][i]);
        const dappOverflowQuestionUser = await deployedDappOverflow.getUser(dappOverflowQuestions[1][i]);
        if (parseInt(dappOverflowQuestion[5]) >= 1) {
          answers = await this.getAnswers(dappOverflowQuestions[0][i], parseInt(dappOverflowQuestion[5]));
        }
        questions.push(dappOverflowQuestions[0][i] + ": " + dappOverflowQuestion + " (" + this.hex2a(dappOverflowQuestionUser) + ") Answers:  [" +  answers + ']');
      }
      this.setStatus('Found questions: ');
      this.setQuestions(questions);
    } catch (e) {
      console.log(e);
      this.setStatus('Error getting length; see log.');
    }
  }

  async getAnswers(questionId, answersLength) {
    console.log('Getting ' + answersLength + ' answers for question ' + questionId);
    var answers = [];
    try {
      const deployedDappOverflow = await this.DappOverflow.deployed();
      console.log("getAnswers("+questionId+", "+answersLength+")");
      const dappOverflowAnswers = await deployedDappOverflow.getAnswers(questionId, answersLength);
      console.log(dappOverflowAnswers);
      for (var j=0; j<dappOverflowAnswers[0].length; j++) {
        const dappOverflowAnswer = await deployedDappOverflow.getAnswer(dappOverflowAnswers[0][j]);
        const dappOverflowAnswerUser = await deployedDappOverflow.getUser(dappOverflowAnswers[1][j]);
        answers.push(dappOverflowAnswers[0][j] + ": " + dappOverflowAnswer + " (" + this.hex2a(dappOverflowAnswerUser) + ")");
      }
      return answers;
    } catch (e) {
      console.log(e);
      return answers;
    }
  }

  hex2a(hexx) {
      var hex = hexx.toString().replace(/0+$/,'').replace(/^0x+/, '');//force conversion
      var str = '';
      for (var i = 0; i < hex.length; i += 2)
          str += String.fromCharCode(parseInt(hex.substr(i, 2), 16));
      return str;
  }

  clickAddress(e) {
    this.account = e.target.value;
  }

  setAmount(e) {
    console.log('Setting amount: ' + e.target.value);
    this.model.amount = e.target.value;
  }

  setReceiver(e) {
    console.log('Setting receiver: ' + e.target.value);
    this.model.receiver = e.target.value;
  }

  setName(e) {
    console.log('Setting name: ' + e.target.value);
    this.model.name = e.target.value;
  }

  setAddress(e) {
    console.log('Setting address: ' + e.target.value);
    this.model.address = e.target.value;
  }

  setQuestion(e) {
    console.log('Question: ' + e.target.value);
    this.model.question = e.target.value;
  }

  setAnswer(e) {
    console.log('Answer: ' + e.target.value);
    this.model.answer = e.target.value;
  }

  setQuestionId(e) {
    console.log('QuestionId: ' + e.target.value);
    this.model.questionId = e.target.value;
  }

}



