interface IQuestion {
    id: any;
    title: string;
    description: string;
    tags: string[];
    votes: number;
    views: number;
    answers: number;
}

export class Question {
  id: any;
  title: string;
  description: string;
  tags: string[];
  votes: number;
  views: number;
  answers: number;

  constructor(obj: IQuestion = {id:'', title:'', description:'', tags:[], votes:0, views:0, answers:0}) {
      this.id = String(obj.id);
      this.title = obj.title;
      this.description = obj.description;
      this.tags = obj.tags;
      this.votes = obj.votes;
      this.views = obj.views;
      this.answers = obj.answers;
  };
}



