import { Injectable } from '@angular/core';
import { Answer } from './answer.class';
import { Question } from './question.class';

@Injectable()
export class QuestionService {

  questions: Question[];

	constructor() {
        this.questions = [];
	}

  // addQuestion(question: Question): void {
  //     this.questions.unshift(question);
  // }

	// getQuestions(): Question[] {
	// 	return this.questions;
	// }

  addQuestion(args: any) {
     let q = new Question({
         id: args.questionId,
         title: args.questionTitle,
         description: args.question,
         tags: args.questionTags.split(','),
         votes: 0,
         views: 0,
         answers: 0
     });
     if (this.questions.indexOf(q) == -1) {
       this.questions.unshift(q);
       return q;
     }
     return null;
  }

  updateQuestion(i: number, args: any): Question {
    this.questions[i].answers = args.answersCount;
    return this.questions[i];
  }

  updateVotes(args: any): Question {
    for (let i in this.questions) {
			if (this.questions[i].id == parseInt(args.questionId)) {
        this.questions[i].votes = args.votes;
        return this.questions[i];
			}
		}
  }

  async getQuestions(dappOverflow) {
    var questions = [];
    console.log('Getting questions');
    try {
      const deployedDappOverflow = await dappOverflow.deployed();
      const dappOverflowQuestions = await deployedDappOverflow.getQuestions();
      var questionsLen = dappOverflowQuestions.length;
      for (var i=0; i<dappOverflowQuestions[0].length; i++) {
        const dappOverflowQuestion = await deployedDappOverflow.getQuestion(dappOverflowQuestions[0][i]);
        //const dappOverflowQuestionUser = await deployedDappOverflow.getUser(dappOverflowQuestions[1][i]);
				let q1 = new Question({
            id: dappOverflowQuestions[0][i],
            title: dappOverflowQuestion[0],
            description: dappOverflowQuestion[1],
            tags: dappOverflowQuestion[2].split(','),
            votes: dappOverflowQuestion[3],
            views: dappOverflowQuestion[4],
            answers: dappOverflowQuestion[5]
        });
				questions.unshift(q1);
      }
      this.questions = questions;
    } catch (e) {
      console.log(e);
    }
    return questions;
  }

  async getAnswers(id, length, dappOverflow) {
    var answers = [];
    console.log('Getting answers');
    try {
      const deployedDappOverflow = await dappOverflow.deployed();
      const dappOverflowAnswers = await deployedDappOverflow.getAnswers(id, parseInt(length));
      console.log(dappOverflowAnswers);
      for (var j=0; j<dappOverflowAnswers[0].length; j++) {
        const dappOverflowAnswer = await deployedDappOverflow.getAnswer(dappOverflowAnswers[0][j]);
        let a = new Answer({
            id: dappOverflowAnswers[0][j],
            answer: dappOverflowAnswer,
            questionId: id
        });
        answers.unshift(a);
      }
      return answers;
    } catch (e) {
      console.log(e);
    }
    return answers;
  }

  getTaggedQuestions(tag): Question[] {
		return this.questions.filter(function(q){
      if(q.tags.indexOf(tag) !== -1){
          return q;
      }
    });
	}

  stringToArray(tags: string): string[] {
    tags = tags.replace(/\s/g,'');
    if(tags.indexOf(",") !== -1){
      return tags.split(',');
    } else {
      return [tags];
    }
  }

    async getQuestion(id: number, dappOverflow) {

        console.log(id);
      //console.log(this.questions);
      // return new Question('q1', 'Updating variable changes in components from a service with angular2', 'My app has a NameService which holds the name. There are two child components of App, Navbar and TheContent which reference this service. Whenever the name changes in the service, i want it to update in both of the other components. How can i do this?', ['javascript', 'angular'], 51, 43413, 3);
      // var result = this.questions.filter(function(q){
      //   console.log(id);
      //   if(q.id === id){
      //       return q;
      //   }
      // });
      //
      // if(result.length > 0){
      //   return result[0];
      // } else {
      //   return new Question();
      // }

      try {
        const deployedDappOverflow = await dappOverflow.deployed();
        const dappOverflowQuestion = await deployedDappOverflow.getQuestion(id);
				let q = new Question({
            id: id,
            title: dappOverflowQuestion[0],
            description: dappOverflowQuestion[1],
            tags: dappOverflowQuestion[2].split(','),
            votes: dappOverflowQuestion[3],
            views: dappOverflowQuestion[4],
            answers: dappOverflowQuestion[5]
        });
        return q;
      } catch (e) {
        console.log(e);
        return new Question();
      }
	}

  hex2a(hexx) {
      var hex = hexx.toString().replace(/0+$/,'').replace(/^0x+/, '');//force conversion
      var str = '';
      for (var i = 0; i < hex.length; i += 2)
          str += String.fromCharCode(parseInt(hex.substr(i, 2), 16));
      return str;
  }
}



