interface IAnswer {
    id: any;
    questionId: any;
    answer: string;
}

export class Answer {
  id: any;
  questionId: any;
  answer: string;

  constructor(obj: IAnswer = {id:0, questionId:'',  answer:''}) {
      this.id = String(obj.id);
      this.questionId = String(obj.questionId);
      this.answer = obj.answer;
  };
}



