import { Component, OnInit } from '@angular/core';
import { QuestionService } from '../question.service';
import { Question } from '../question.class';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-question-list-tagged',
  templateUrl: './question-list-tagged.component.html',
  styleUrls: ['../question-list/question-list.component.css']
})
export class QuestionListTaggedComponent implements OnInit {

    private sub;
    tag: string;
    questions: Question[];

    constructor(private questionService: QuestionService, private route: ActivatedRoute) { }

	ngOnInit() {
        this.sub = this.route.params.subscribe(params => {
            console.log(params.tag);
            this.tag = params.tag;
            this.questions = this.getTaggedQuestions(this.tag);
        });
	}

    ngOnDestroy() {
        this.sub.unsubscribe();
    }

	getTaggedQuestions(tag): Question[] {
		console.log(this.questionService.getTaggedQuestions(tag));
		return this.questionService.getTaggedQuestions(tag);
	}
}



