import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { QuestionService } from '../question.service';
import { Question } from '../question.class';
import { DataService } from '../dataservice';


@Component({
	selector: 'app-add-question',
	templateUrl: './add-question.component.html',
	styleUrls: ['../question-list/question-list.component.css', '../view-question/view-question.component.css', './add-question.component.css']
})
export class AddQuestionComponent implements OnInit {

    q: Question;
	account: string;
	tagsStr: string;
	DappOverflow: any;
	deployedDappOverflow: any;
	blockNumber = 0;

	constructor(private questionService: QuestionService, private router: Router, private dataService: DataService) { }

	ngOnInit() {

		this.dataService.getAccount().then((account) => {
			this.account = account;
		});

		this.dataService.accountsObservable.subscribe((accounts) => {
			if (this.account != accounts[0]) {
				this.account = accounts[0];
			}
		});

		this.dataService.getDappOverflow().then((DappOverflow) => {
			console.log('Got contract');
			this.DappOverflow = DappOverflow;
			this.deployedDappOverflow = this.DappOverflow.deployed();
			this.watchEvents(this);

		});

		this.q = new Question();
	}

    // addQuestion(): void {
    //     this.questionService.addQuestion(this.q);
	// this.router.navigateByUrl('/questions/'+this.q.id);
    // }

	watchEvents(e) {
		e.deployedDappOverflow.then(function(instance) {
			instance.QuestionSubmitted().watch((err, response) => {
				if (!err) {
					if (e.blockNumber < response.blockNumber) {
						if (response.args.questionId == parseInt(e.q.id)) {
							e.blockNumber = response.blockNumber;
							e.router.navigateByUrl('/questions/' + e.q.id);
						}
					}
				} else {
					console.log(err);
				}
			});
		});
	}

	async addQuestion() {

		if (!this.DappOverflow) {
			console.log('not found', this.DappOverflow);
			return;
		}
		console.log(this.DappOverflow);

		const questionId = this.q.id;
		const questionTitle = this.q.title;
		const questionDescription = this.q.description;
		const questionTags = this.tagsStr;

		try {
			const deployedDappOverflow = await this.DappOverflow.deployed();
			console.log("deployed", deployedDappOverflow);
			const transaction = await deployedDappOverflow.submitQuestion(questionId, questionTitle, questionDescription, questionTags, this.account, { from: this.account });
			console.log("transaction", transaction);
			document.getElementsByClassName("loading")[0].classList.add("in");
		} catch (e) {
			console.log(e);
		}
    }

    createId(): void {
        //this.q.id = this.q.title.split(" ").join("_").substring(0,10) + "_" + (new Date().getTime());
		this.q.id = new Date().getTime();
    }

    addTags(tags): void {
		this.tagsStr = tags;
		this.q.tags = tags.split(",").filter(function(t) {
			if (t.trim().length > 0) return t.trim();
		});
    }

}



