import { Component, OnInit } from '@angular/core';
import { QuestionService } from '../question.service';
import { Question } from '../question.class';
import { DataService } from '../dataservice';


@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {

  account: string;
  DappOverflow: any;
  deployedDappOverflow: any;

  constructor(private questionService: QuestionService, private dataService: DataService) {
      // this.questions = questionService.getQuestions();
  }

  blockNumber = 0;

  ngOnInit() {
    this.dataService.getAccount().then((account) => {
      this.account = account;
    });

    this.dataService.accountsObservable.subscribe((accounts) => {
      if (this.account != accounts[0]) {
        this.account = accounts[0];
      }
    });

    this.dataService.getDappOverflow().then((DappOverflow) => {
        this.DappOverflow = DappOverflow;
        this.deployedDappOverflow = this.DappOverflow.deployed();
        this.getQuestions();
        this.watchEvents(this);

    });
  }

  watchEvents(e) {
    e.deployedDappOverflow.then(function(instance) {
       instance.QuestionSubmitted().watch((err, response) => {
        if (!err) {
           if (e.blockNumber < response.blockNumber) {
              e.blockNumber = response.blockNumber;
   						e.addQuestion(response.args);
           }
        } else {
          console.log(err);
        }
       });
    });
  }

  tags = [];

  questions = [];

  getTags(questions){
      // return [];
      //console.log(questions);
      let tempTags = [];
      if (questions.length > 0) {
        for(let i=0; i <questions.length; i++){
            let q = questions[i];
            for(let j=0; j <q.tags.length; j++){
                let t = q.tags[j];
                if(tempTags.indexOf(t) === -1){
                    tempTags.push(t);
                }
            }
        }
      }
      this.tags = tempTags;
  }

  async getQuestions() {
		//console.log(this.questionService.getQuestions(this.DappOverflow));
		this.questions = await this.questionService.getQuestions(this.DappOverflow);
    this.getTags(this.questions);

	}

  async addQuestion(args) {
    let newQ = await this.questionService.addQuestion(args);
		if (newQ != null) {
			this.questions.unshift(newQ);
      this.getTags(this.questions);
		}
	}

}



