import { Component, OnInit } from '@angular/core';
import { QuestionService } from '../question.service';
import { Answer } from '../answer.class';
import { Question } from '../question.class';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../dataservice';

@Component({
	selector: 'app-view-question',
	templateUrl: './view-question.component.html',
	styleUrls: ['../question-list/question-list.component.css', './view-question.component.css', '../sidebar/sidebar.component.css']
})
export class ViewQuestionComponent implements OnInit {

		a: Answer;
    id: number;
    question: Question;
	answers: Answer[];
    private sub;

	account: string;
	DappOverflow: any;
	deployedDappOverflow: any;

	blockNumber = 0;

	constructor(private questionService: QuestionService, private route: ActivatedRoute, private dataService: DataService) { }

	ngOnInit() {

		this.answers = [];

		this.a = new Answer();

        this.sub = this.route.params.subscribe(params => {
            console.log(params.question);
            this.id = parseInt(params.question);
						this.question = new Question();

						this.dataService.getAccount().then((account) => {
							this.account = account;
						});

						this.dataService.accountsObservable.subscribe((accounts) => {
							if (this.account != accounts[0]) {
								this.account = accounts[0];
							}
						});

						this.dataService.getDappOverflow().then((DappOverflow) => {
							this.DappOverflow = DappOverflow;
							this.deployedDappOverflow = this.DappOverflow.deployed();
							this.getQuestion(this.id);
							this.watchEvents(this);
							if (this.account != undefined) {								
								this.updateViews();
							}
						});
        });

	}



    ngOnDestroy() {
        this.sub.unsubscribe();
    }

	watchEvents(e) {
		e.deployedDappOverflow.then(function(instance) {
			instance.VoteSubmitted().watch((err, response) => {
				if (!err) {
					if (e.blockNumber < response.blockNumber) {
						console.log("----------------VoteSubmitted");
						e.blockNumber = response.blockNumber;
						e.updateVotes(response.args);
					}
				} else {
					console.log(err);
				}
			});
			instance.AnswerSubmitted().watch((err, response) => {
			 if (!err) {
					if (e.blockNumber < response.blockNumber) {
						console.log("----------------AnswerSubmitted");
						e.blockNumber = response.blockNumber;
						console.log(response.args);
						//e.getAnswers(response.args.userQuestionId, response.args.answersCount);
						e.answers.unshift(new Answer({
							id: response.args.answerId,
							questionId: response.args.userQuestionId,
							answer: response.args.answer
						}));
						e.a = new Answer();
					}
			 } else {
				 console.log(err);
			 }
			});
		});
	}

	async getQuestion(id: number) {
		//console.log(this.questionService.getQuestion(id, this.DappOverflow));
		this.question = await this.questionService.getQuestion(id, this.DappOverflow);
		this.getAnswers(this.question.id, this.question.answers);
	}

	async getAnswers(id: number, length: number) {
		this.answers = await this.questionService.getAnswers(id, length, this.DappOverflow);
	}

	async updateViews() {
		if (!this.DappOverflow) {
			return;
		}

		try {
			const deployedDappOverflow = await this.DappOverflow.deployed();
			const transaction = await deployedDappOverflow.updateViews(this.id, { from: this.account });
		} catch (e) {
			console.log(e);
		}

	}

	async upvote() {
		if (!this.DappOverflow) {
			return;
		}

		try {
			const deployedDappOverflow = await this.DappOverflow.deployed();
			const transaction = await deployedDappOverflow.upvote(this.id, { from: this.account });
		} catch (e) {
			console.log(e);
		}

	}

	async downvote() {
		if (!this.DappOverflow) {
			return;
		}

		try {
			const deployedDappOverflow = await this.DappOverflow.deployed();
			const transaction = await deployedDappOverflow.downvote(this.id, { from: this.account });
		} catch (e) {
			console.log(e);
		}

	}

	async updateVotes(args) {
		this.question = await this.questionService.updateVotes(args);
	}

	async addAnswer(newAnswer: string) {

		if (!this.DappOverflow) {
			console.log('not found', this.DappOverflow);
			return;
		}
		console.log(this.DappOverflow);

		// answer, userAddress, questionId

		const questionId = this.question.id;
		const answer = this.a.answer;

		try {
			const deployedDappOverflow = await this.DappOverflow.deployed();
			console.log("deployed", deployedDappOverflow);
			console.log(deployedDappOverflow.submitAnswer)
			const transaction = await deployedDappOverflow.submitAnswer(newAnswer, this.account, questionId, { from: this.account });
			console.log("transaction", transaction);
			// document.getElementsByClassName("loading")[0].classList.add("in");
		} catch (e) {
			console.log(e);
		}
	}

}



